import React from 'react';
import { createRoot } from 'react-dom/client';
import CriarUnidadeReact from '../components/CriarUnidadeReact';

// Exportar componente para window (para testes e uso global)
window.CriarUnidadeReact = CriarUnidadeReact;

// Renderizar automaticamente se container existir
const rootDiv = document.getElementById('criar-unidade-root');
if (rootDiv) {
  const root = createRoot(rootDiv);
  root.render(React.createElement(CriarUnidadeReact));
}